import { useContext } from 'react'
// import { Currency } from '@pancakeswap/sdk'
import { Flex } from '@pancakeswap/uikit'
import { AppBody } from 'components/App'

import { useTheme as useNextTheme } from 'next-themes'

// import { useCurrency } from '../../hooks/Tokens'
// import { Field } from '../../state/swap/actions'
// import { useSwapState, useSingleTokenSwapInfo } from '../../state/swap/hooks'
import Page from '../Page'
// import PriceChartContainer from './components/Chart/PriceChartContainer'

import SwapForm from './components/SwapForm'
// import StableSwapFormContainer from './StableSwap'
import { StyledInputCurrencyWrapper, StyledSwapContainer } from './styles'
// import SwapTab, { SwapType } from './components/SwapTab'
import { SwapFeaturesContext } from './SwapFeaturesContext'

export default function Swap() {
  // const { isMobile } = useMatchBreakpoints()
  const { isChartExpanded } = useContext(SwapFeaturesContext)


  const { setTheme } = useNextTheme()
  if (typeof window !== "undefined" && setTheme) {
    window.ReSetTheme = setTheme
  }

  return (
    <Page removePadding={isChartExpanded} hideFooterOnDesktop={isChartExpanded}>
      <Flex width={['100%']} height="100%" justifyContent="center" position="relative">
        <Flex width={['100%']} flexDirection="column">
          <StyledSwapContainer $isChartExpanded={isChartExpanded}>
            <StyledInputCurrencyWrapper mt={isChartExpanded ? '24px' : '0'}>
              <AppBody>
               <SwapForm />
              </AppBody>
            </StyledInputCurrencyWrapper>
          </StyledSwapContainer>
        </Flex>
      </Flex>
    </Page>
  )
}
